/* App.css */

body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f9;
  color: #333;
}

.bg {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

header {
  text-align: center;
  margin-bottom: 20px;
}

h1 {
  color: #4a90e2;
  font-size: 2rem;
  margin-bottom: 10px;
}

.input-container .mid-input-box {
  display: flex;
  justify-content: center;
  gap: 10px;
}

input[type="text"] {
  width: 250px;
  padding: 10px;
  border: 2px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  outline: none;
}

input[type="text"]:focus {
  border-color: #4a90e2;
}

button {
  background-color: green;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: rgb(29, 165, 29);
}

nav ul {
  display: flex;
  gap: 15px;
  padding: 0;
  list-style: none;
  margin: 20px 0;
}

nav li {
  padding: 10px 15px;
  background-color: #eee;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

nav li:hover {
  background-color: rgb(29, 165, 29);
}

.navActive {
  background-color: green;
  color: white;
}

h4 {
  margin-top: 30px;
  font-size: 1.2rem;
  color: #555;
}

.section {
  margin-top: 10px;
  padding: 20px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 80%;
  max-width: 600px;
  text-align: center;
}

.section h2 {
  font-size: 1.5rem;
  color: #4a90e2;
}
